.card {
  display: flex;
  justify-content: center;
  padding: 0 20px;
  margin: 0 auto;
  width: 400px;
  min-height: 500px;
}

.wrap {
  cursor: pointer;
  padding: 60px 0;
  position: relative;
  transform-style: preserve-3d;
}

.wrap:hover .img {
  transform: scale(1.015);
}

.wrap:hover .title:after {
  width: 90%;
}

.profile {
  position: relative;
  max-width: 290px;
  padding: 25px;
  padding-bottom: 255px;
  background: white;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), 0 3px 5px rgba(0, 0, 0, 0.05);
  transform-style: preserve-3d;
  z-index: 5;
}

.profile .img {
  position: absolute;
  width: 100%;
  height: 300px;
  right: -59px;
  bottom: -55px;
  border-style: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 9px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transform-style: preserve-3d;
  transform: translateZ(15px);
  transition: all 0.5s;
}

@media (max-width: 500px) {
  .card {
    padding: 20px;
  }
  .profile .img {
    right: 0;
  }
}
